<template>
  <div class="paymentRoomManager">
    <!-- <van-nav-bar
      title="缴费管理"
      left-arrow
      :right-text="rightTitle"
      @click-left="onClickLeft()"
      @click-right="deleteFn()"
    />  -->
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
          <div class="rightBtn" @click="deleteFn">{{rightTitle}}</div>
      </template>
    </navigation-bar>

    <div class="paymentRoomManagerContent">
      <div style="margin-top: 45px;">
        <div class="content" v-for="(area,index) in areaList" :key="index">
          <div v-bind:class="area.isShow?'areaHead': 'areaHeadBack'" @click="arrowDir(area)">
            <div class="areaName">{{area.area_name}}</div>
            <div class="areaImg">
              <van-image class="orderImg" :src="area.isShow?arrowDownImg:arrowRightImg" />
            </div>
          </div>
          <div
            v-show="area.isShow==true"
            style="box-shadow: 0 0 10px 0 rgba(225,225,225,0.50);border-radius: 6px;margin-top: 10px;z-index: 100;"
          >
            <div v-for=" (room,roomIndex) in area.roomList" class="navText" :key="roomIndex">
              <div
                v-if="room.is_owner==0&&isShowDelete==true"
                style="margin-top: 3px;margin-left: 15px;"
              >
                <van-image width="20" height="20" :src="deleteImg" @click="deleteConfirm(room)" />
              </div>
              <div class="areaLine">{{room.room_name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div style="height: 80px;"></div>
    </div>

    <div class="bottomPanel">
      <div class="addRoom" @click="addNewRoom">+ 新增缴费</div>
    </div>
  </div>
</template>

<script>
import {
  queryAreaInfoService,
  delRoomownerService
} from "../../../services/propertyBillService";
import { Dialog } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "paymentRoomManager",
  mounted() {
    this.queryAreaInfo();
  },
  components: {
    NavigationBar
  },
  data() {
    return {
      areaList: [],
      activeNames: ["1"],
      deleteImg: require("../../../assets/images/home/property/payment/list_btn_delete.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_white.png"),
      rightTitle: "编辑",
      isShowDelete: false,
      headerItemWidth:{
        width:'84px'
      },
      title:'缴费管理',
    };
  },
  methods: {
    queryAreaInfo() {
      var reqData = {
        owner_code: this.$userInfo.OWNER_CODE
      };
      queryAreaInfoService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          if (result.result) {
            let areas = result.data.areaList;
            for(let index in result.data.areaList){
              result.data.areaList[index].isShow = false;
            }
            this.areaList = result.data.areaList;
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    arrowDir(area) {
      console.log(JSON.stringify(area));
      area.isShow = !area.isShow;
    },
    deleteFn() {
      this.isShowDelete = !this.isShowDelete;
      this.rightTitle = this.rightTitle == "编辑" ? "完成" : "编辑";
      this.areaList.forEach((item, index) => {
      if(this.rightTitle == "编辑")
        item.isShow = false;
        else
        item.isShow = true;
      });
    },
    deleteConfirm(room) {
      Dialog.confirm({
        title: "提示",
        message: "确认解除绑定？"
      })
        .then(() => {
          // on confirm
          this.houseBindingdeleteFn(room);
        })
        .catch(() => {
          // on cancel
        });
    },
    houseBindingdeleteFn(room) {
      console.log(room);
      var reqData = {
        ROOM_CODE: room.room_code,
        OWNER_CODE: room.owner_code,
        USER_ID: this.$userInfo.OWNER_CODE
      };
      delRoomownerService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          if (result.result) {
            this.$toast("您已成功解绑房间");
            this.queryAreaInfo();
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      this.$router.back();
    },
    addNewRoom(){
      this.$router.push('./selectArea')
    }
  }
};
</script>

<style scoped>
.paymentRoomManager {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.paymentRoomManager .paymentRoomManagerContent{
  flex: 1;
  overflow-y: scroll;
}
.paymentRoomManager .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.paymentRoomManager .content {
  margin-top: 22px;
  position: relative;
  width: 90%;
  margin-left: 5%;
  margin-bottom: 22px;
}
.bottomPanel {
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  background-color: #fff;
  z-index: 100;
}
.bottomPanel .addRoom {
  font-size: 16px;
  color: #fb5768;
  background: #ffffff;
  box-shadow: 0 0 8px 0 rgba(189, 189, 189, 0.29);
  border-radius: 6px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  margin: 16px;
}

.paymentRoomManager .areaHead {
  background: #fb5364;
  border-radius: 6px;
  height: 48px;
  display: flex;
  line-height: 48px;
  padding-left: 20px;
  color: #fff;
  font-size: 14px;
}

.paymentRoomManager .areaHeadBack {
  background: #f6f6f8;
  border-radius: 6px;
  height: 48px;
  display: flex;
  line-height: 48px;
  padding-left: 20px;
  font-size: 14px;
}

.paymentRoomManager .areaLine {
  margin-left: 16px;
  font-size: 14px;
  color: #3a3a3a;
}

.paymentRoomManager .areaImg {
  text-align: right;
  position: absolute;
  right: 5%;
}

.paymentRoomManager .navText {
  display: flex;
  width: 100%;
  border-bottom: 1px solid #ececec;
  height: 48px;
  line-height: 48px;
  font-size: 14px;
}
.paymentRoomManager .orderImg {
    width: 50%;
    float: right;
    margin: 15px 0;
}
</style>
